import React from 'react';

import('../css/keypad.css');

class Keypad extends React.PureComponent {
  constructor(props) {
    super(props);

    const defaultLabel = (<span className="txtGray">mmddyy</span>);

    this.state = {
      digits: [],
      defaultLabel: defaultLabel,
      label: defaultLabel
    };
  };

  addDigit = event => {
    const digits = this.state.digits.slice();

    digits.push(event.target.textContent);

    const label = digits.length > 0 ? digits.join('') : this.state.defaultLabel;

    if (digits.length === 6) {
      this.props.setField(digits.join(''));
    } else {
      this.setState({
        digits,
        label
      });
    }
  };

  clearDigits = () => {
    this.props.setField(null);
  };

  removeDigit = () => {
    const digits = this.state.digits.slice(0, this.state.digits.length - 1);
    const label = digits.length > 0 ? digits.join('') : this.state.defaultLabel;

    this.setState({
      digits,
      label
    });
  };

  render() {
    return (
      <div>
        <div className="absolute full-screen flex justifyCenter alignCenter">
          <div className="overlay absolute full-screen bgWhite" onClick={this.props.closeModal}/>
          <div className={`keypad absolute bdrBlack p5 bgWhite ${this.props.className}`}>
            <p className="mb5 bdrBlack p5 txtC">{this.state.label}</p>
            <div className="flex mb5">
              <p className="key mr5 bdrBlack csrPointer" onClick={this.addDigit}>1</p>
              <p className="key mr5 bdrBlack csrPointer" onClick={this.addDigit}>2</p>
              <p className="key bdrBlack csrPointer" onClick={this.addDigit}>3</p>
            </div>
            <div className="flex mb5">
              <p className="key mr5 bdrBlack csrPointer" onClick={this.addDigit}>4</p>
              <p className="key mr5 bdrBlack csrPointer" onClick={this.addDigit}>5</p>
              <p className="key bdrBlack csrPointer" onClick={this.addDigit}>6</p>
            </div>
            <div className="flex mb5">
              <p className="key mr5 bdrBlack csrPointer" onClick={this.addDigit}>7</p>
              <p className="key mr5 bdrBlack csrPointer" onClick={this.addDigit}>8</p>
              <p className="key bdrBlack csrPointer" onClick={this.addDigit}>9</p>
            </div>
            <div className="flex">
              <p className="key mr5 bdrBlack csrPointer" onClick={this.removeDigit}>&lt;</p>
              <p className="key mr5 bdrBlack csrPointer" onClick={this.addDigit}>0</p>
              <p className="key bdrBlack csrPointer" onClick={this.clearDigits}>x</p>
            </div>
          </div>
        </div>
      </div>
    );
  };
}

export default Keypad;
