import React from 'react';

class Filters extends React.PureComponent {
  render() {
    const sortButton           = this.props.isAsc          ? 'fa-caret-square-up' : 'fa-caret-square-down';
    const bookButtonMode       = this.props.showBooks      ? '' : 'txtRed';
    const comicButtonMode      = this.props.showComics     ? '' : 'txtRed';
    const movieButtonMode      = this.props.showMovies     ? '' : 'txtRed';
    const dateButtonMode       = this.props.showDate       ? '' : 'txtRed';
    const duplicatesButtonMode = this.props.showDuplicates ? '' : 'txtRed';

    return (
      <div className="flex flex-col m-flex-row spaceBetween alignCenter mb5">
        <div className="flex alignCenter mr5 mb5 m-mb0 flex100 m-flex1">
          <i aria-hidden={true} className={`mr5 fas ${bookButtonMode} fa-book fs14 csrPointer`} onClick={this.props.toggleShowBooks}></i>
          <i aria-hidden={true} className={`mr5 fas ${comicButtonMode} fa-book-open fs14 csrPointer`} onClick={this.props.toggleShowComics}></i>
          <i aria-hidden={true} className={`mr5 fas ${movieButtonMode} fa-film fs14 csrPointer`} onClick={this.props.toggleShowMovies}></i>
          <i aria-hidden={true} className={`mr5 fas ${dateButtonMode} fa-calendar fs14 csrPointer`} onClick={this.props.toggleShowDate}></i>
          <i aria-hidden={true} className={`mr5 fas ${duplicatesButtonMode} fa-clone fs14 csrPointer`} onClick={this.props.toggleShowDuplicates}></i>
          <i aria-hidden={true} className={`mr5 fas ${sortButton} fs14 csrPointer`} onClick={this.props.toggleIsAsc}></i>
        </div>
        <div className="flex flex100 m-flex1">
          <div className="mr5 flex33 m-w100">
            <p
            className="bdrBox bdrBlack p5 flex100 csrPointer"
            id="dateFrom"
            name="dateFrom"
            onClick={() => {this.props.setKeypad(true, 'dateFrom')}}
            >
              {this.props.dateFrom.label || (<span className="txtGray">from</span>)}
            </p>
          </div>
          <div className="mr5 flex33 m-w100">
            <p
            className="bdrBox bdrBlack p5 flex100 csrPointer"
            id="dateTo"
            name="dateTo"
            onClick={() => {this.props.setKeypad(true, 'dateTo')}}
            >
              {this.props.dateTo.label || (<span className="txtGray">to</span>)}
            </p>
          </div>
          <div className="flex33 m-w100">
            <input
            className="bdrBox bdrBlack p5 flex100"
            name="title"
            onChange={this.props.filterTitle}
            placeholder="title"
            value={this.props.title}
            />
          </div>
        </div>
      </div>
    );
  }
}

export default Filters;
