import React from 'react';

import SimpleDate from '../SimpleDate';

import { daysOfTheWeek, monthsOfTheYear } from '../constants';
import * as utils from '../utils';

import('../css/calendar.css');

class Calendar extends React.PureComponent {
  setDatesToMonth = () => {
    const daysInMonth = utils.getDaysInMonth(this.props.month, this.props.year);
    const month       = utils.padDate(this.props.month)
    const year        = this.props.year;
    const dateFrom    = new SimpleDate(`${month}/01/${year}`);
    const dateTo      = new SimpleDate(`${month}/${daysInMonth}/${year}`);

    this.props.setDates(dateFrom, dateTo);
  };

  render() {
    const daysInMonth    = utils.getDaysInMonth(this.props.month, this.props.year);
    const firstDayOfWeek = new Date(this.props.year, this.props.month - 1, 1).getDay();
    const lastDayOnPage  = (firstDayOfWeek + daysInMonth) % 7 > 0 ? (Math.floor((firstDayOfWeek + daysInMonth) / 7) + 1) * 7 : 0;

    let labels          = [];
    let daysBeforeMonth = [];
    let daysAfterMonth  = [];
    let daysOfMonth     = [];
    let itemsInMonth    = [];

    this.props.items.forEach(item => {
      if (item.date.year === this.props.year && item.date.month === this.props.month) {
        itemsInMonth.push(item);
      }
    });

    for (let i = 0; i < daysOfTheWeek.length; i++) {
      labels.push(
        <div className="day bdrBox mb5 bdrBlack p5 txtC truncate" key={daysOfTheWeek[i]}>
          {daysOfTheWeek[i]}
        </div>
      );
    }

    for (let i = 0; i < firstDayOfWeek; i++) {
      daysBeforeMonth.push(
        <div className="day bdrBox mb5 bdrGray p5" key={`before${i}`}></div>
      );
    }

    for (let i = 0; i < daysInMonth; i++) {
      const dayOfMonth = i + 1;
      const day   = utils.padDate(dayOfMonth);
      const month = utils.padDate(this.props.month);
      const year  = this.props.year;
      const date  = new SimpleDate(`${month}/${day}/${year}`);

      const itemsOfDay = itemsInMonth.map((item, index) => {
        if (item.date.day === dayOfMonth) {
          let icon = 'book';

          if (item.type === 'comic') {
            icon = 'book-open';
          } else if (item.type === 'movie') {
            icon = 'film';
          }

          return (<i aria-hidden={true} className={`mr5 fas fa-${icon}`} key={index}></i>);
        }

        return null;
      });

      daysOfMonth.push(
        <div
        className="day bdrBox mb5 bdrBlack p5 csrPointer"
        key={`day${i}`}
        onClick={() => {this.props.setDates(date, date)}}
        >
          <p>{i + 1}</p>
          <p>{itemsOfDay}</p>
        </div>
      );
    }

    for (let i = firstDayOfWeek + daysInMonth; i < lastDayOnPage; i++) {
      daysAfterMonth.push(
        <div className="day bdrBox mb5 bdrGray p5" key={`after${i}`}></div>
      );
    }

    return (
      <section id="calendar">
        <div className="flex spaceBetween alignCenter bdrBox mb5 bdrBlack p5">
          <i aria-hidden={true} className={`mr5 fs14 fas fa-chevron-circle-left csrPointer`} onClick={this.props.setMonthPrevious}></i>
          <p className="mr5 csrPointer" onClick={this.setDatesToMonth}>{monthsOfTheYear[this.props.month - 1]} {this.props.year}</p>
          <i aria-hidden={true} className={`fs14 fas fa-chevron-circle-right csrPointer`} onClick={this.props.setMonthNext}></i>
        </div>
        <div className="flex flex-wrap bdrBox">
          {labels}
          {daysBeforeMonth}
          {daysOfMonth}
          {daysAfterMonth}
        </div>
      </section>
    );
  }
}

export default Calendar;
